/* 
Fonter:
Br√∂dtext: font-family: 'Roboto Mono', monospace;
Rubriker och logo: font-family: 'Cinzel', serif;
Citat i header: font-family: 'Yellowtail', cursive; 
Discount text: font-family: 'Permanent Marker', cursive;
*/
/* Color variables */
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

p {
  font-family: "Roboto Mono", monospace;
  font-size: 0.9rem;
}

h1,
h2,
h3,
h4 {
  font-family: "Cinzel", serif;
  font-weight: 700;
  color: #FFFFFF;
}

h1,
h3,
h4 {
  font-size: 1.5rem;
}

h2 {
  font-size: 0.75rem;
}

.zig-zag-border {
  fill: #7BC9A1;
}

header {
  background-color: #46433A;
  position: relative;
}
header .logo-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
header .logo-section .business-logo {
  width: 6rem;
  margin-top: 3.75rem;
}
header .logo-section .business-name {
  margin-top: 0.625rem;
}
header .logo-section .business-quoat {
  margin: 1.25rem 0 1.875rem 0;
  font-family: "Yellowtail", cursive;
  font-size: 1rem;
  color: #F2CC43;
  margin-top: 1.25rem;
}
header nav {
  position: fixed !important;
  top: 0;
  background-color: #46433A;
  width: 100vw;
  z-index: 2;
}
header nav .container-fluid .icon-toggle {
  height: 2.5rem;
  width: 2.5rem;
  border: none;
  outline: none;
  color: #FFFFFF;
  font-size: 2rem;
}
header nav .container-fluid .icon-toggle .menu-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}
header nav .container-fluid .icon-toggle[aria-expanded=false] .icon-expanded {
  display: none;
}
header nav .container-fluid .icon-toggle[aria-expanded=true] .icon-collapsed {
  display: none;
}
header nav .navbar-collapse .navbar-nav {
  height: 20rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
header nav .navbar-collapse .navbar-nav .nav-link {
  text-align: center;
  font-family: "Cinzel", serif;
  color: #FFFFFF;
  font-size: 1.5rem;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
header .zig-zag-border {
  position: absolute;
  bottom: -1px;
  z-index: 1;
}

.about-us-section {
  background-color: #7BC9A1;
  padding-left: 16px;
  padding-right: 15px;
}
.about-us-section .about-us-wrapper-left {
  height: 50%;
}
.about-us-section .about-us-wrapper-left .about-us-title {
  color: #FFFFFF;
  display: inline-block;
  font-family: "Cinzel";
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 48px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding-top: 28.89px;
}
.about-us-section .about-us-wrapper-left .about-us-text {
  padding-top: 10px;
  padding-bottom: 45px;
  color: #46433A;
  font-weight: 400;
  line-height: 27px;
}
.about-us-section .about-us-wrapper-right {
  display: none;
}

.discount-section {
  background-color: #7BC9A1;
  text-align: center;
}

.about-us-discount-top {
  padding-top: 49px;
  padding-bottom: 30px;
  font-size: 1.5rem;
  font-family: "Permanent Marker";
  font-weight: 400;
  line-height: 35px;
  color: #FFFFFF;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.about-us-discount-bottom {
  padding-top: 36px;
  padding-bottom: 33px;
  font-size: 1.5rem;
  color: #FFFFFF;
  font-family: "Permanent Marker";
  font-weight: 400;
  line-height: 35px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.mug-pic {
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  transition-duration: 4s;
  transition: 0.4s ease-in-out;
  transition-property: transform;
  animation-name: grow;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  -webkit-transition-property: -webkit-transform;
  -webkit-animation-name: grow;
  -webkit-animation-duration: 4s;
  -moz-transition-property: -moz-transform;
  -moz-animation-name: grow;
  -moz-animation-duration: 4s;
}

@keyframes grow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@-webkit-keyframes grow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
.drinks-section {
  background-image: url("../img/coffee-pattern-background.png");
  background-color: rgba(247, 246, 243, 0.5);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0px 15px 15px 20px;
}

.drinks-section-title {
  color: #46433A;
  text-transform: uppercase;
  font-family: "Cinzel";
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 48px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding-top: 28.89px;
}

.drinks-name {
  margin-left: 25px;
  font-weight: bold;
}

.cookie-box {
  background-color: #46433A;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 49px;
  padding-bottom: 68px;
  width: 90%;
  position: fixed;
  top: 30%;
  left: 5%;
}
.cookie-box .cookie-text {
  color: #F2CC43;
  text-transform: uppercase;
  width: 50%;
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 60px;
}
.cookie-box .cookie-wrapper {
  display: flex;
  gap: 23px;
}
.cookie-box .cookie-wrapper .cookie-btn {
  text-transform: uppercase;
  background-color: #8CE1B6;
  color: #FFFFFF;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  font-weight: 700;
  height: 38px;
  width: 120px;
  font-family: "Roboto Mono";
}

.hidden {
  display: none;
}

@media screen and (min-width: 576px) {
  header nav {
    position: relative !important;
  }
  header nav .navbar-collapse .navbar-nav {
    width: 100%;
    height: 3rem;
    flex-direction: row;
  }
  header .zig-zag-border {
    z-index: 3;
  }
}
@media screen and (min-width: 834px) {
  p {
    font-size: 1rem;
  }
  .about-us-section {
    display: flex;
    flex-direction: row;
    gap: 5rem;
  }
  .about-us-section .about-us-wrapper-left {
    margin-left: 60px;
  }
  .about-us-section .about-us-wrapper-left .about-us-title {
    font-size: 3rem;
    line-height: 54px;
  }
  .about-us-section .about-us-wrapper-right {
    display: block;
    text-align: center;
    padding-right: 58px;
    color: #FFFFFF;
  }
  .about-us-section .about-us-discount-top {
    margin-top: 47px;
    padding: 0;
  }
  .discount-section {
    display: none;
  }
  .cookie-box {
    flex-direction: row;
    gap: 66px;
    padding-top: 60px;
    padding-bottom: 70px;
  }
  .cookie-box .cookie-text {
    font-size: 1.5rem;
    margin-top: 63px;
  }
  .cookie-box .cookie-wrapper {
    flex-direction: column;
  }
  .cookie-box .cookie-wrapper .cookie-btn {
    width: 200px;
    height: 50px;
  }
}
@media screen and (min-width: 1440px) {
  p {
    font-size: 1rem;
  }
  .cookie-box .cookie-text {
    width: 650px;
    margin-right: 5%;
  }
  .cookie-box .cookie-wrapper {
    flex-direction: row;
  }
}